// ModeFormReducer.js
const initialState = {
  isOpen: false,
  isRecipeEditing: false,
  modeForm: {
    groupId: '',
    groupName: '',
    modeId: '',
    name: '',
    availabilityLevel: '',
    type: 'predefined',
    start: [],
    end: [],
    created_at: '',
  },
};

const ModeFormReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'MODEFORM_OPEN':
      if (action.modeForm) {
        return {
          ...state,
          isOpen: true,
          modeForm: action.modeForm,
        };
      } else {
        return {
          ...state,
          isOpen: true,
          modeForm: initialState['modeForm'],
        };
      }

    case 'MODEFORM_CLOSE':
      return {
        ...state,
        isOpen: false,
        modeForm: {
          groupId: '',
          groupName: '',
          modeId: '',
          name: '',
          availabilityLevel: '',
          type: 'predefined',
          start: [],
          end: [],
          created_at: '',
        },
      };
    case 'MODEFORM_RECIPE_EDIT':
      return {
        ...state,
        isRecipeEditing: true,
      };
    case 'MODEFORM_STORE_EDIT':
      return {
        ...state,
        isRecipeEditing: false,
      };
    default:
      return state;
  }
};

export default ModeFormReducer;
