import React, { useState, useEffect, useMemo } from 'react';
import axios from 'axios';
import getCookie from '../../Routers/getCookie';
import styled from 'styled-components';
import { useParams, useNavigate } from 'react-router-dom';
import Modal from 'react-modal';
import { toast } from 'react-toastify';
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd';
import BaseComponent from '../BaseComponent';
import JsonEditor from './JsonEditorComponent';
import ModeForm from './ModeFormComponent';
import KanbanStoreComponent from './KanbanStoreComponent';
import SelectedKanbanComponent from './SelectedKanbanComponent';
import RenderJsonAsKanbanComponent from './RenderJsonAsKanbanComponent';
import { useSelector, useDispatch } from 'react-redux';
import { ClipLoader } from 'react-spinners';

const SpinnerContainer = styled.div`
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const BlurOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ButtonContainer = styled.div`
  margin: 20px 0;
  background-color: #000;
  display: flex;
  gap: 10px;
  justify-content: right;
`;

const JsonEditButton = styled.a`
  text-decoration: none;
  background-color: #fff;
  color: #000;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #aaa;
  }
`;

const CloseButton = styled.span`
  position: absolute;
  color: #000;
  cursor: pointer;
`;

const ModalDiv = styled.div`
  width: 100%;
  max-height: 80vh;
  object-fit: contain;
`;

const ModalImage = styled.img`
  width: 100%;
  max-height: 80vh;
  object-fit: contain;
`;

const customModalStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    overflow: 'auto',
  },
};

const Container = styled.div`
  display: flex;
`;

const IntroduceDiv = styled.div`
  margin-bottom: 40px;
`;

const KanbanContainer = styled.div`
  width: 800px;
  margin: 20px;
  background-color: #fff;
  color: #000;
  padding: 20px;
  border-radius: 5px;

  @media (max-width: 600px) {
    width: 100%; /* 미디어 쿼리에서 크기를 100%로 변경 */
  }
`;

const KanbanSelectedContainer = styled.div`
  width: 800px;
  margin: 20px;
  background-color: #eee;
  color: #000;
  padding: 20px;
  border-radius: 5px;

  @media (max-width: 600px) {
    width: 100%; /* 미디어 쿼리에서 크기를 100%로 변경 */
  }
`;
const VerticalKanbanList = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`;

// 동적으로 필드 이름과 색상을 매핑하는 맵
const dynamicFieldColorMap = new Map();

const JsonKanbanComponent = () => {
  const { jsonUrl } = useParams();
  const file = useSelector((state) => state.file.file);
  const editedJson = useSelector((state) => state.editedJson.editedJson);
  const modalIsOpen = useSelector((state) => state.modal.modalIsOpen);
  const modalContent = useSelector((state) => state.modal.modalContent);
  const modeformIsOpen = useSelector((state) => state.modeform.isOpen);
  const kanbanList = useSelector((state) => state.kanban.kanban);
  const loading = useSelector((state) => state.loading.loading);
  const dispatch = useDispatch();

  useEffect(() => {
    const fetchData = async () => {
      dispatch({ type: 'LOADING' });
      try {
        const response = await axios.get(jsonUrl);
        if (response.status === 200) {
          const jsonData = response.data;
          dispatch({
            type: 'SET_JSON',
            editedJson: JSON.stringify(jsonData, null, 2),
          });
        } else {
          console.error('Failed to fetch JSON data');
        }
      } catch (error) {
        console.error('Error fetching JSON data:', error);
        toast.error('Error fetching JSON data');
      } finally {
        dispatch({ type: 'NOT_LOADING' });
      }
    };
    fetchData();
  }, []);

  const openJsonEditor = () => {
    openModal(
      <JsonEditor
        jsonUrl={jsonUrl}
        onSave={handleJsonSave}
        onClose={closeModal}
      />,
    );
  };

  const handleJsonSave = (editedJson) => {
    console.log('Edited JSON:', editedJson);
  };

  const openModal = (content) => {
    dispatch({ type: 'OPEN_MODAL', content: content });
  };

  // Function to close the modal
  const closeModal = () => {
    dispatch({ type: 'CLOSE_MODAL' });
  };

  const onDragEnd = (result) => {
    const { destination, source, draggableId } = result;
    console.log('result : ', result);

    try {
      const draggedItem = kanbanList[source.index];
      const droppedId = destination.droppableId;
      // Mode 추가 케이스
      if (droppedId.startsWith('selected') || droppedId.startsWith('render')) {
        console.log(JSON.parse(editedJson));
        let parsedJson = JSON.parse(editedJson).concat(draggedItem);
        let data = JSON.stringify(parsedJson, null, 2);

        dispatch({
          type: 'SET_JSON',
          editedJson: data, // editedJson = parsedJson
        });

        // DB File Model Editing process

        axios({
          method: 'POST',
          url: 'http://127.0.0.1:8000/api/edit_file/',
          data: {
            file_path: jsonUrl,
            file_data: { data: parsedJson },
          },
          headers: {
            'Content-Type': 'application/json',
            'X-CSRFToken': getCookie('csrftoken'),
          },
        })
          .then((response) => {
            if (response.data['success']) {
              toast.success('모드 수정 성공!');
            } else {
              toast.error('모드 수정 에러!');
            }
          })
          .catch((error) => {
            toast.error('모드 수정 에러! ', error);
          });
      } else {
        toast.error('잘못된 구역에 드롭하였습니다.');
      }
    } catch (error) {
      toast.error('잘못된 구역에 드롭하였습니다.');
      console.log(error);
    }
  };

  return (
    <BaseComponent>
      <ButtonContainer>
        <JsonEditButton onClick={() => openJsonEditor()}>
          Edit Recipe For Raw
        </JsonEditButton>
        <JsonEditButton onClick={() => dispatch({ type: 'MODEFORM_OPEN' })}>
          Create Mode
        </JsonEditButton>
      </ButtonContainer>
      <Container>
        {loading && (
          <BlurOverlay>
            <SpinnerContainer>
              <ClipLoader size={35} color={'#000'} />
            </SpinnerContainer>
          </BlurOverlay>
        )}
        <DragDropContext onDragEnd={(result) => onDragEnd(result)}>
          {modeformIsOpen && <ModeForm />}
          <KanbanContainer>
            <IntroduceDiv>
              🤡 모드를 생성, 수정, 삭제할 수 있습니다. 모드를 드래그 앤
              드롭으로 추가할 수 있습니다.
            </IntroduceDiv>
            <KanbanStoreComponent />
          </KanbanContainer>
          <KanbanSelectedContainer>
            <IntroduceDiv>
              🤡 레시피가 현재 가지고 있는 모드입니다.
            </IntroduceDiv>
            <SelectedKanbanComponent />
          </KanbanSelectedContainer>
          <KanbanSelectedContainer>
            <IntroduceDiv>
              🤡 레시피의 현재 구조를 한눈에 파악할 수 있습니다.
            </IntroduceDiv>
            <RenderJsonAsKanbanComponent data={editedJson} />
          </KanbanSelectedContainer>
        </DragDropContext>
      </Container>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel="Preview Modal"
        style={customModalStyles}
      >
        <CloseButton onClick={closeModal}>&times;</CloseButton>
        {typeof modalContent === 'string' ? (
          <ModalImage src={modalContent} alt="Preview Image" />
        ) : (
          <ModalDiv>{modalContent}</ModalDiv>
        )}
      </Modal>
    </BaseComponent>
  );
};

export default JsonKanbanComponent;
