import React from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';

import { useSelector, useDispatch } from 'react-redux';
import { initialState } from '../Reducers/UserReducer';
import reducerStore from '../Reducers/ReducerStore';
import useEventTimeout from '../utils/TimeoutUtil';

const MainContainer = styled.div`
  font-family: Arial, sans-serif;
  background-color: #000;
  color: #fff;
  padding: 30px;
`;

const Header = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Title = styled.h1`
  cursor: pointer;
  margin: 0;
`;

const Buttons = styled.div`
  display: flex;
  gap: 10px;
`;

const Button = styled.a`
  text-decoration: none;
  background-color: #fff;
  color: #000;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #aaa;
  }
`;

const BaseComponent = ({ children }) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const isLogin = localStorage.getItem('isLogin');

  useEventTimeout((message) => {
    if (isLogin === 'true') {
      toast.info(message);
      if (message === '제한 시간 종료!') {
        dispatch({ type: 'LOGOUT' }); // Reducer를 사용하여 로그인 액션 디스패치
        navigate('/login');
      }
    }
  }, []);

  return (
    <MainContainer>
      <Header>
        <Title onClick={() => navigate('/')}>FILE SERVICE</Title>
        {isLogin === 'true' && (
          <Buttons>
            <Button onClick={() => navigate('/user_profile')}>
              User Profile
            </Button>
            <Button
              onClick={() => {
                dispatch({ type: 'LOGOUT' }); // Reducer를 사용하여 로그인 액션 디스패치
                navigate('/login');
              }}
            >
              Logout
            </Button>
          </Buttons>
        )}
      </Header>
      {children}
    </MainContainer>
  );
};

export default BaseComponent;
