// LoadingReducer.js
const initialState = {
  loading: false, // 파일 목록을 저장하는 배열
};

const loadingReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'LOADING':
      // 파일 목록을 업데이트
      return {
        ...state,
        loading: true,
      };
    case 'NOT_LOADING':
      return {
        ...state,
        loading: false,
      };
    default:
      return state;
  }
};

export default loadingReducer;
