import React, { useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import BaseComponent from './BaseComponent';
import { toast } from 'react-toastify';
import axios from 'axios';
import 'react-toastify/dist/ReactToastify.css'; // CSS 스타일링을 불러옵니다.
import { ClipLoader } from 'react-spinners'; // You can choose a specific loader from the library
import getCookie from '../Routers/getCookie';
import { useSelector, useDispatch } from 'react-redux';
import { initialState } from '../Reducers/UserReducer';
import reducerStore from '../Reducers/ReducerStore';

const Button = styled.a`
  text-decoration: none;
  background-color: #000;
  color: #fff;
  margin: 10px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #555;
  }
`;

const H2Black = styled.h2`
  font-family: 'Helvetica', 'Arial', sans-serif;
  color: #000;
  line-height: 1.5;
  font-size: 1.5rem;
  margin-top: 5px;
  margin-left: 10px;
  margin-bottom: 5px;
`;

const PBlack = styled.p`
  font-family: 'Helvetica', 'Arial', sans-serif;
  color: #000;
  line-height: 1.5;
  font-size: 1rem;
  margin-left: 10px;
  margin-bottom: 20px;
`;

const MainBody = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const ButtonContainer = styled.div`
  margin: 20px;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
`;

const SpinnerContainer = styled.div`
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const BlurOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(
    255,
    255,
    255,
    0.5
  ); // Adjust the background color and opacity as needed
  backdrop-filter: blur(5px); // Adjust the blur amount as needed
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const UploadComponent = () => {
  const isLogin = useSelector((state) => state.user.isLogin);
  const loading = useSelector((state) => state.loading.loading);
  const [selectedFile, setSelectedFile] = useState(null);
  const fileInputRef = useRef(null);

  const navigate = useNavigate();
  const dispatch = useDispatch(); // useDispatch를 사용하여 액션 디스패치

  const handleFileChange = (e) => {
    const file = e.target.files[0];
    setSelectedFile(file);
  };

  function showToast(message) {
    toast.success(message, {
      position: 'bottom-right', // Toast 메시지의 위치 설정 (원하는 위치로 수정 가능)
      autoClose: 3000, // Toast 메시지가 자동으로 사라지는 시간 (밀리초 단위, 3000은 3초)
      style: {
        backgroundColor: '#333',
        color: '#fff',
        border: '2px solid #fff',
      },
    });
  }

  function handleUpload() {
    if (selectedFile) {
      dispatch({ type: 'LOADING' });
      const formData = new FormData();
      formData.append('file', selectedFile);

      axios({
        method: 'POST',
        url: 'http://127.0.0.1:8000/api/upload/',
        data: formData,
        headers: {
          'Content-Type': 'multipart/form-data',
          'X-CSRFToken': getCookie('csrftoken'),
        },
      })
        .then((response) => {
          if (response.status === 200) {
            // 업로드가 성공한 경우, 원하는 작업을 수행할 수 있습니다.
            showToast('파일이 성공적으로 업로드되었습니다.');
            console.log('파일이 성공적으로 업로드되었습니다.');

            // 파일 목록을 업데이트
            axios.get('http://127.0.0.1:8000/api/files/').catch((error) => {
              console.error('파일 목록 업데이트 오류:', error);
            });
          } else {
            throw new Error('파일 업로드에 실패했습니다.');
          }
        })
        .catch((error) => {
          showToast('파일 업로드 오류');
          console.error('파일 업로드 오류:', error);
        })
        .finally(() => {
          dispatch({ type: 'NOT_LOADING' });
        });
    }
  }
  function clearUploadPage() {
    setSelectedFile(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = ''; // 파일 입력 엘리먼트 초기화
    }
  }

  return (
    <BaseComponent>
      <MainBody>
        <ButtonContainer>
          <H2Black>Upload File</H2Black>
          <form>
            <input
              type="file"
              accept="*"
              style={{ display: 'none' }} // 숨김 스타일을 적용
              ref={fileInputRef}
              onChange={handleFileChange}
            />

            {selectedFile ? (
              <PBlack>Selected File: {selectedFile.name}</PBlack>
            ) : (
              <PBlack>Selected File: 선택된 파일이 없습니다.</PBlack>
            )}

            <Button onClick={() => fileInputRef.current.click()}>
              Select File
            </Button>
            <Button
              onClick={() => {
                handleUpload();
                clearUploadPage();
              }}
            >
              Upload
            </Button>
            <Button onClick={() => navigate('/')}>Back to File List</Button>
          </form>

          {loading && (
            <BlurOverlay>
              <SpinnerContainer>
                <ClipLoader size={35} color={'#000'} />
              </SpinnerContainer>
            </BlurOverlay>
          )}
        </ButtonContainer>
      </MainBody>
    </BaseComponent>
  );
};

export default UploadComponent;
