// SelectedKanbanComponent.jsx
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import styled from 'styled-components';
import { toast } from 'react-toastify';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import getCookie from '../../Routers/getCookie';
import { useSelector, useDispatch } from 'react-redux';
import formatDate from '../../utils/Util';

const KanbanList = styled.div`
  display: flex;
  flex-direction: column;
  border: 1px solid #666; /* Adjust border color */
  margin-top: 30px;
  padding: 16px; /* Increase padding for better spacing */
  border-radius: 8px;
  background-color: #f9f9f9; /* Add a subtle background color */
`;

const KanbanTable = styled.table`
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
`;

const KanbanTh = styled.th`
  padding: 7px;

  text-align: center;
  border-bottom: 2px solid #ddd;
  border-right: 2px solid #ddd;
  font-weight: bold;
`;

const KanbanTd = styled.td`
  padding: 7px;
  border-bottom: 2px solid #ddd;
  border-right: 2px solid #ddd;
  text-align: center;
`;

const KanbanFeatureId = styled.div`
  font-weight: bold;
`;

const KanbanOptions = styled.div`
  margin-top: 5px;
  text-align: center;
  color: #777;
`;

const KanbanHeaderRow = styled.tr`
  width: 100%;
  background-color: #eee;
`;

const KanbanStartEndRow = styled.tr`
  width: 100%;
  background-color: #eee;
`;

const KanbanButtonContainer = styled.div`
  font-family: 'Arial', sans-serif;
  text-align: right;
`;

const EditButton = styled.button`
  margin-right: 8px;
  padding: 6px 12px;
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #444;
  }
`;

const DeleteButton = styled.button`
  padding: 6px 12px;
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #444;
  }
`;

const SelectedKanbanComponent = () => {
  const dispatch = useDispatch();
  const loading = useSelector((state) => state.loading.loading);
  const editedJson = useSelector((state) => state.editedJson.editedJson);
  const filePath = useSelector((state) => state.file.file.file);
  const handleEdit = (kanbanGroup) => {
    dispatch({ type: 'MODEFORM_RECIPE_EDIT' });
    dispatch({ type: 'MODEFORM_OPEN', modeForm: kanbanGroup });
  };

  const handleDelete = async (modeId) => {
    dispatch({ type: 'LOADING' });
    try {
      let parsedJson = JSON.parse(editedJson);

      const indexToDelete = parsedJson.findIndex(
        (item) => item.modeId === modeId,
      );

      if (indexToDelete !== -1) {
        // Remove the item from the array
        parsedJson.splice(indexToDelete, 1);
      }

      dispatch({
        type: 'SET_JSON',
        editedJson: JSON.stringify(parsedJson, null, 2),
      });
      const postData = {
        file_path: filePath,
        file_data: parsedJson,
      };

      axios({
        method: 'POST',
        url: 'http://127.0.0.1:8000/api/edit_file/',
        data: postData,
        headers: {
          'Content-Type': 'application/json',
          'X-CSRFToken': getCookie('csrftoken'),
        },
      })
        .then((response) => {
          if (response.data['success']) {
            toast.success(modeId + ' 모드 삭제 성공!');
          } else {
            toast.error(modeId + '모드를 삭제하는 도중 에러가 발생했습니다.');
          }
        })
        .catch((error) => {
          toast.error(modeId + '모드를 삭제하는 도중 에러가 발생했습니다.');
        });
    } catch (error) {
      toast.error('모드가 삭제되지 못했습니다.', error);
    } finally {
      dispatch({ type: 'NOT_LOADING' });
    }
  };

  if (loading) {
    return <div> 칸반 데이터를 불러오는 중입니다 ... </div>;
  }

  const kanbanList = JSON.parse(editedJson);

  return kanbanList.map((kanbanGroup, groupIndex) => (
    <Droppable
      key={`selected-kanban-key-${groupIndex}`}
      droppableId={`selected-kanban-${groupIndex}`}
      type="CARD"
    >
      {(provided) => (
        <div ref={provided.innerRef} {...provided.droppableProps}>
          <KanbanList>
            <KanbanButtonContainer>
              <EditButton onClick={() => handleEdit(kanbanGroup)}>
                Edit
              </EditButton>
              <DeleteButton onClick={() => handleDelete(kanbanGroup.modeId)}>
                Delete
              </DeleteButton>
            </KanbanButtonContainer>
            <KanbanTable>
              <thead>
                <KanbanHeaderRow>
                  <KanbanTh>ModeId</KanbanTh>
                  <KanbanTh>ModeName</KanbanTh>
                  <KanbanTh>GroupId</KanbanTh>
                  <KanbanTh>GroupName</KanbanTh>
                  <KanbanTh>AvailabilityLevel</KanbanTh>
                </KanbanHeaderRow>
              </thead>
              <tbody>
                <tr>
                  <KanbanTd>{kanbanGroup.modeId}</KanbanTd>
                  <KanbanTd>{kanbanGroup.name}</KanbanTd>
                  <KanbanTd>{kanbanGroup.groupId}</KanbanTd>
                  <KanbanTd>{kanbanGroup.groupName}</KanbanTd>
                  <KanbanTd>{kanbanGroup.availabilityLevel}</KanbanTd>
                </tr>
              </tbody>
            </KanbanTable>

            <div>
              <strong>Start:</strong>
              <KanbanTable>
                <thead>
                  <KanbanStartEndRow>
                    <KanbanTh>FeatureId</KanbanTh>
                    <KanbanTh>ControlId</KanbanTh>
                    <KanbanTh>Option</KanbanTh>
                  </KanbanStartEndRow>
                </thead>
                <tbody>
                  {kanbanGroup.start.map((action, actionIndex) => (
                    <tr key={actionIndex}>
                      <KanbanTd>
                        <KanbanFeatureId>{action.featureId}</KanbanFeatureId>
                      </KanbanTd>
                      <KanbanTd>
                        <KanbanOptions>
                          {action.options.map((options, optionIndex) => (
                            <div key={optionIndex}>{options.controlId}</div>
                          ))}
                        </KanbanOptions>
                      </KanbanTd>
                      <KanbanTd>
                        <KanbanOptions>
                          {action.options.map((options, optionIndex) => (
                            <div key={optionIndex}>{options.option}</div>
                          ))}
                        </KanbanOptions>
                      </KanbanTd>
                    </tr>
                  ))}
                </tbody>
              </KanbanTable>
            </div>

            <div>
              <strong>End:</strong>
              <KanbanTable>
                <thead>
                  <KanbanStartEndRow>
                    <KanbanTh>FeatureId</KanbanTh>
                    <KanbanTh>ControlId</KanbanTh>
                    <KanbanTh>Option</KanbanTh>
                  </KanbanStartEndRow>
                </thead>
                <tbody>
                  {kanbanGroup.end.map((action, actionIndex) => (
                    <tr key={actionIndex}>
                      <KanbanTd>
                        <KanbanFeatureId>{action.featureId}</KanbanFeatureId>
                      </KanbanTd>
                      <KanbanTd>
                        <KanbanOptions>
                          {action.options.map((options, optionIndex) => (
                            <div key={optionIndex}>{options.controlId}</div>
                          ))}
                        </KanbanOptions>
                      </KanbanTd>
                      <KanbanTd>
                        <KanbanOptions>
                          {action.options.map((options, optionIndex) => (
                            <div key={optionIndex}>{options.option}</div>
                          ))}
                        </KanbanOptions>
                      </KanbanTd>
                    </tr>
                  ))}
                </tbody>
              </KanbanTable>
            </div>
          </KanbanList>
          {provided.placeholder}
        </div>
      )}
    </Droppable>
  ));
};

export default SelectedKanbanComponent;
