const initialState = {
  kanban: [], // 파일 목록을 저장하는 배열
};

const KanbanReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'SET_KANBAN':
      return {
        ...state,
        kanban: action.kanban,
      };
    default:
      return state;
  }
};

export default KanbanReducer;
