import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import styled from 'styled-components';
import { ClipLoader } from 'react-spinners';
import BaseComponent from './BaseComponent';
import { useSelector, useDispatch } from 'react-redux';

const LoginContainer = styled.div`
  text-align: center;
`;

const LoginTitle = styled.h2`
  font-size: 24px;
  margin-bottom: 20px;
`;

const LoginForm = styled.form`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const FormGroup = styled.div`
  margin-bottom: 10px;
  text-align: left;
`;

const Label = styled.label`
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
`;

const InputField = styled.input`
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
`;

const LoginButton = styled.button`
  background-color: #fff;
  color: #000;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #3e0;
    font-weight: bold;
  }
`;

const SpinnerContainer = styled.div`
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const BlurOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const RegisterLink = styled.p`
  margin-top: 10px;
  font-size: 16px;

  a {
    text-decoration: none;
    color: #0074cc;
    font-weight: bold;
    cursor: pointer;
    transition: text-decoration 0.2s;

    &:hover {
      text-decoration: underline;
      font-weight: bold;
    }
  }
`;

const LoginComponent = () => {
  const navigate = useNavigate();
  const loading = useSelector((state) => state.loading.loading);
  const formState = useSelector((state) => state.form);
  const dispatch = useDispatch();

  useEffect(() => {
    if (localStorage.getItem('isLogin') === 'true') {
      dispatch({ type: 'LOGOUT' });
    }
  }, []);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    dispatch({ type: 'UPDATE_FIELD', fieldName: name, fieldValue: value });
  };

  const resetForm = () => {
    dispatch({ type: 'RESET_FORM' });
  };

  const handleSubmit = async (e) => {
    dispatch({ type: 'LOADING' });
    e.preventDefault();
    try {
      const csrfToken = getCookie('csrftoken');
      const response = await fetch('http://127.0.0.1:8000/api/login/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-CSRFToken': csrfToken,
        },
        body: JSON.stringify(formState),
        credentials: 'include',
      });
      if (response.ok) {
        toast.success('로그인 성공');
        // JSON 문자열 변환 없이 그대로 사용
        const responseData = await response.json();
        responseData['rawPassword'] = formState.password;
        const payload = JSON.stringify(responseData);
        console.log(payload);
        dispatch({
          type: 'LOGIN',
          payload: payload,
        });
        navigate('/');
      } else {
        toast.error('등록되지 않은 사용자입니다. 가입 후 이용해주세요.');
      }
    } catch (error) {
      console.error('로그인 오류:', error);
      toast.error('로그인 중에 오류가 발생했습니다.');
      resetForm();
    } finally {
      dispatch({ type: 'NOT_LOADING' });
      resetForm();
    }
  };

  const getCookie = (name) => {
    var cookieValue = null;
    if (document.cookie && document.cookie !== '') {
      var cookies = document.cookie.split(';');
      for (var i = 0; i < cookies.length; i++) {
        var cookie = cookies[i].trim();
        if (cookie.substring(0, name.length + 1) === name + '=') {
          cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
          break;
        }
      }
    }
    return cookieValue;
  };

  return (
    <BaseComponent>
      <LoginContainer>
        <LoginTitle>Login</LoginTitle>
        {loading && (
          <BlurOverlay>
            <SpinnerContainer>
              <ClipLoader size={35} color={'#000'} />
            </SpinnerContainer>
          </BlurOverlay>
        )}

        <LoginForm onSubmit={handleSubmit}>
          <FormGroup>
            <Label htmlFor="username">Username</Label>
            <InputField
              type="text"
              id="username"
              name="username"
              value={formState.username}
              onChange={handleInputChange}
            />
          </FormGroup>
          <FormGroup>
            <Label htmlFor="password">Password</Label>
            <InputField
              type="password"
              id="password"
              name="password"
              value={formState.password}
              onChange={handleInputChange}
            />
          </FormGroup>
          <LoginButton type="submit">Login</LoginButton>
        </LoginForm>
        <RegisterLink>
          Don't have an account?{' '}
          <a href={() => false} onClick={() => navigate('/registration')}>
            Register
          </a>
        </RegisterLink>
      </LoginContainer>
    </BaseComponent>
  );
};

export default LoginComponent;
