// KanbanStoreComponent.jsx

import React, { useState, useEffect } from 'react';
import axios from 'axios';
import styled from 'styled-components';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import { toast } from 'react-toastify';
import { useSelector, useDispatch } from 'react-redux';
import formatDate from '../../utils/Util';

const KanbanList = styled.div`
  display: flex;
  flex-direction: column;
  border: 1px solid #666; /* Adjust border color */
  margin-top: 30px;
  padding: 16px; /* Increase padding for better spacing */
  border-radius: 8px;
  background-color: #f9f9f9; /* Add a subtle background color */
`;

const KanbanTable = styled.table`
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
`;

const KanbanTh = styled.th`
  padding: 10px;
  text-align: center;
  border-bottom: 2px solid #ddd;
  border-right: 2px solid #ddd;
  font-weight: bold;
`;

const KanbanTd = styled.td`
  padding: 10px;
  border-bottom: 2px solid #ddd;
  border-right: 2px solid #ddd;
  text-align: center;
`;

const KanbanFeatureId = styled.div`
  font-weight: bold;
`;

const KanbanOptions = styled.div`
  margin-top: 5px;
  text-align: center;
  color: #777;
`;

const KanbanHeaderRow = styled.tr`
  width: 100%;
  background-color: #eee;
`;

const KanbanStartEndRow = styled.tr`
  width: 100%;
  background-color: #eee;
`;

const KanbanButtonContainer = styled.div`
  font-family: 'Arial', sans-serif; /* Change font family */
  text-align: right; /* Align text to the left */
`;

const EditButton = styled.button`
  margin-right: 8px;
  padding: 6px 12px;
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #444;
  }
`;

const DeleteButton = styled.button`
  padding: 6px 12px;
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #444;
  }
`;

const KanbanStoreComponent = () => {
  const [jsonDataList, setJsonDataList] = useState([]);
  const kanbanList = useSelector((state) => state.kanban.kanban);
  const loading = useSelector((state) => state.loading.loading);
  const dispatch = useDispatch();

  useEffect(() => {
    const fetchJsonDataList = async () => {
      dispatch({ type: 'LOADING' });
      try {
        const response = await axios.get(
          'http://127.0.0.1:8000/api/json-data-list/',
        );
        if (response.status === 200) {
          dispatch({ type: 'SET_KANBAN', kanban: response.data });
        } else {
          console.error('Failed to fetch JSON data list');
        }
      } catch (error) {
        console.error('Error fetching JSON data list:', error);
      } finally {
        dispatch({ type: 'NOT_LOADING' });
      }
    };
    fetchJsonDataList();
  }, []);

  const renderJsonAsKanbanList = (kanbanList) => {
    return kanbanList.map((kanbanGroup, groupIndex) => (
      <Draggable
        key={`store-kanban-${groupIndex}`}
        draggableId={`store-kanban-${groupIndex}`}
        index={groupIndex}
      >
        {(provided) => (
          <div
            ref={provided.innerRef}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
          >
            <KanbanList>
              <KanbanButtonContainer>
                <EditButton onClick={() => handleEdit(kanbanGroup)}>
                  Edit
                </EditButton>
                <DeleteButton
                  onClick={() =>
                    handleDelete(kanbanGroup.modeId, kanbanGroup.created_at)
                  }
                >
                  Delete
                </DeleteButton>
              </KanbanButtonContainer>
              <KanbanTable>
                <thead>
                  <KanbanHeaderRow>
                    <KanbanTh>ModeId</KanbanTh>
                    <KanbanTh>ModeName</KanbanTh>
                    <KanbanTh>GroupId</KanbanTh>
                    <KanbanTh>GroupName</KanbanTh>
                    <KanbanTh>AvailabilityLevel</KanbanTh>
                  </KanbanHeaderRow>
                </thead>
                <tbody>
                  <tr>
                    <KanbanTd>{kanbanGroup.modeId}</KanbanTd>
                    <KanbanTd>{kanbanGroup.name}</KanbanTd>
                    <KanbanTd>{kanbanGroup.groupId}</KanbanTd>
                    <KanbanTd>{kanbanGroup.groupName}</KanbanTd>
                    <KanbanTd>{kanbanGroup.availabilityLevel}</KanbanTd>
                  </tr>
                </tbody>
              </KanbanTable>

              <div>
                <strong>Start:</strong>
                <KanbanTable>
                  <thead>
                    <KanbanStartEndRow>
                      <KanbanTh>FeatureId</KanbanTh>
                      <KanbanTh>ControlId</KanbanTh>
                      <KanbanTh>Option</KanbanTh>
                    </KanbanStartEndRow>
                  </thead>
                  <tbody>
                    {kanbanGroup.start.map((action, actionIndex) => (
                      <tr key={actionIndex}>
                        <KanbanTd>
                          <KanbanFeatureId>{action.featureId}</KanbanFeatureId>
                        </KanbanTd>
                        <KanbanTd>
                          <KanbanOptions>
                            {action.options.map((options, optionIndex) => (
                              <div key={optionIndex}>{options.controlId}</div>
                            ))}
                          </KanbanOptions>
                        </KanbanTd>
                        <KanbanTd>
                          <KanbanOptions>
                            {action.options.map((options, optionIndex) => (
                              <div key={optionIndex}>{options.option}</div>
                            ))}
                          </KanbanOptions>
                        </KanbanTd>
                      </tr>
                    ))}
                  </tbody>
                </KanbanTable>
              </div>

              <div>
                <strong>End:</strong>
                <KanbanTable>
                  <thead>
                    <KanbanStartEndRow>
                      <KanbanTh>FeatureId</KanbanTh>
                      <KanbanTh>ControlId</KanbanTh>
                      <KanbanTh>Option</KanbanTh>
                    </KanbanStartEndRow>
                  </thead>
                  <tbody>
                    {kanbanGroup.end.map((action, actionIndex) => (
                      <tr key={actionIndex}>
                        <KanbanTd>
                          <KanbanFeatureId>{action.featureId}</KanbanFeatureId>
                        </KanbanTd>
                        <KanbanTd>
                          <KanbanOptions>
                            {action.options.map((options, optionIndex) => (
                              <div key={optionIndex}>{options.controlId}</div>
                            ))}
                          </KanbanOptions>
                        </KanbanTd>
                        <KanbanTd>
                          <KanbanOptions>
                            {action.options.map((options, optionIndex) => (
                              <div key={optionIndex}>{options.option}</div>
                            ))}
                          </KanbanOptions>
                        </KanbanTd>
                      </tr>
                    ))}
                  </tbody>
                </KanbanTable>
              </div>
            </KanbanList>
          </div>
        )}
      </Draggable>
    ));
  };

  const handleEdit = (kanbanGroup) => {
    dispatch({ type: 'MODEFORM_STORE_EDIT' });
    dispatch({ type: 'MODEFORM_OPEN', modeForm: kanbanGroup });
  };

  const handleDelete = async (modeId, created_at) => {
    dispatch({ type: 'LOADING' });

    try {
      await axios.delete('http://127.0.0.1:8000/api/delete-json-data/', {
        data: { created_at: created_at },
        headers: {
          'Content-Type': 'application/json',
        },
      });

      const response = await axios.get(
        'http://127.0.0.1:8000/api/json-data-list/',
      );
      if (response.status === 200) {
        toast.success(modeId + '칸반이 삭제되었습니다');
        dispatch({ type: 'SET_KANBAN', kanban: response.data });
      } else {
        toast.error(modeId + '칸반이 삭제되지 못했습니다');

        console.error('Failed to fetch JSON data list');
      }
    } catch (error) {
      toast.error(modeId + '칸반이 삭제되지 못했습니다');
      console.error('Error deleting or fetching JSON data list:', error);
    } finally {
      dispatch({ type: 'NOT_LOADING' });
    }
  };

  return (
    <div>
      <Droppable droppableId="kanban-store" type="CARD">
        {(provided) => (
          <div ref={provided.innerRef} {...provided.droppableProps}>
            {loading ? (
              <div> 칸반 데이터를 불러오는 중입니다...</div>
            ) : (
              renderJsonAsKanbanList(kanbanList)
            )}
            {provided.placeholder}
          </div>
        )}
      </Droppable>
    </div>
  );
};

export default KanbanStoreComponent;
