// ReducerStore.js
import { createStore, applyMiddleware, combineReducers } from 'redux';
import thunk from 'redux-thunk';
import { persistStore, persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage'; // defaults to localStorage for web

import userReducer from './UserReducer';
import fileReducer from './FileReducer';
import modalReducer from './ModalReducer';
import loadingReducer from './LoadingReducer';
import formReducer from './FormReducer';
import ModeFormReducer from './ModeFormReducer';
import KanbanReducer from './KanbanReducer';
import EditedJsonReducer from './EditedJsonReducer';

// 미들웨어 설정
const middleware = [thunk];

// 루트 리듀서 생성
const rootReducer = combineReducers({
  user: userReducer,
  file: fileReducer,
  editedJson: EditedJsonReducer,
  form: formReducer,
  modal: modalReducer,
  loading: loadingReducer,
  modeform: ModeFormReducer,
  kanban: KanbanReducer,
});

// Redux Persist 구성
const persistConfig = {
  key: 'root',
  storage,
};

const persistedReducer = persistReducer(persistConfig, rootReducer);

// 스토어 생성
export const ReducerStore = createStore(
  persistedReducer,
  applyMiddleware(...middleware),
);

export const persistor = persistStore(ReducerStore);
