import React, { useState, useEffect } from 'react';
import { useDropzone } from 'react-dropzone';
import styled from 'styled-components';
import BaseComponent from './BaseComponent';
import { useNavigate } from 'react-router-dom';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css'; // CSS 스타일링을 불러옵니다
import Modal from 'react-modal'; // Import Modal from react-modal
import { ClipLoader } from 'react-spinners'; // You can choose a specific loader from the library
import axios from 'axios';
import getCookie from '../Routers/getCookie';
import DragComponent from './DragComponent';
import { useSelector, useDispatch } from 'react-redux';
import { initialState } from '../Reducers/UserReducer';
import reducerStore from '../Reducers/ReducerStore';

