// date_joined를 원하는 형식으로 변환
const formatDate = (isoDate) => {
  const options = {
    year: 'numeric',
    month: 'long',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit',
  };
  return new Date(isoDate).toLocaleDateString('ko-KR', options);
};

export default formatDate;
