// FormReducer.js
const initialState = {
  username: '', // 사용자 이름
  password: '', // 비밀번호
  // 다른 폼 필드 추가 가능
};

const formReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'UPDATE_FIELD':
      return {
        ...state,
        [action.fieldName]: action.fieldValue,
      };
    case 'RESET_FORM':
      return initialState;
    default:
      return state;
  }
};

export default formReducer;
