import React, { useState, useEffect } from 'react';
import axios from 'axios';
import styled from 'styled-components';
import ReactJson from 'react-json-view';

const EditorContainer = styled.div`
  padding: 20px;
  background-color: #000;
  border: 1px solid #ddd;
  border-radius: 5px;
  position: relative;
`;

const LoadingMessage = styled.p`
  font-size: 16px;
  color: #333;
`;

const JsonEditor = ({ jsonUrl, onSave, onClose }) => {
  const [editedJson, setEditedJson] = useState('');
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(jsonUrl);
        if (response.status === 200) {
          const jsonData = response.data;
          console.log(jsonData);
          setEditedJson(JSON.stringify(jsonData, null, 2));
        } else {
          console.error('Failed to fetch JSON data');
        }
      } catch (error) {
        console.error('Error fetching JSON data:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [jsonUrl]);

  const handleSave = () => {
    try {
      const parsedJson = JSON.parse(editedJson);
      onSave(parsedJson);
      onClose();
    } catch (error) {
      console.error('Invalid JSON format');
      // Handle invalid JSON format, e.g., show an error message
    }
  };

  return (
    <EditorContainer>
      {loading ? (
        <LoadingMessage>Loading...</LoadingMessage>
      ) : (
        <div>
          <ReactJson
            src={JSON.parse(editedJson)}
            theme="monokai"
            onEdit={({ updated_src }) =>
              setEditedJson(JSON.stringify(updated_src, null, 2))
            }
            onDelete={({ updated_src }) =>
              setEditedJson(JSON.stringify(updated_src, null, 2))
            }
            onAdd={({ updated_src }) =>
              setEditedJson(JSON.stringify(updated_src, null, 2))
            }
          />
          <button onClick={handleSave}>Save</button>
          <button onClick={onClose}>Cancel</button>
        </div>
      )}
    </EditorContainer>
  );
};

export default JsonEditor;
