// FileReducer.js
const initialState = {
  file: null, // 현재 Editing 중인 파일
};

const fileReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'SET_FILE':
      return {
        ...state,
        file: action.file,
      };
    case 'CLEAR_FILE':
      return {
        ...state,
        file: null,
      };

    default:
      return state;
  }
};

export default fileReducer;
