// EditedJsonReducer.js
const initialState = {
  editedJson: null, // 현재 Editing 중인 파일
};

const fileReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'SET_JSON':
      return {
        ...state,
        editedJson: action.editedJson,
      };
    case 'CLEAR_JSON':
      return {
        ...state,
        editedJson: null,
      };

    default:
      return state;
  }
};

export default fileReducer;
