// ModeFormComponent.jsx
import React, { useState, useEffect, useMemo } from 'react';
import styled from 'styled-components';
import Modal from 'react-modal';
import axios from 'axios';
import getCookie from '../../Routers/getCookie';
import { toast } from 'react-toastify';

import { useSelector, useDispatch } from 'react-redux';

const CloseButton = styled.span`
  position: absolute;
  color: #000;
  cursor: pointer;
`;

const ModalDiv = styled.div`
  width: relative;
  max-height: 80vh;
  object-fit: contain;
`;

const ModalImage = styled.img`
  width: relative;
  max-height: 80vh;
  object-fit: contain;
`;

const customModalStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    overflow: 'auto',
  },
};

const KanbanList = styled.div`
  display: flex;
`;

const Container = styled.div`
  display: flex;
`;

const KanbanContainer = styled.div`
  flex: 1;
  margin-top: 20px;
  background-color: #ecf0f1;
  padding: 20px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
`;

const VerticalKanbanList = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`;

const FormContainer = styled.div`
  width: ${({ modalWidth }) => `${modalWidth}px`};
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  max-height: 80vh;
`;

const FormHeader = styled.h2`
  font-size: 1.5rem;
  margin-bottom: 20px;
`;

const FormBody = styled.div`
  display: flex;
  flex-direction: column;
`;

const FormRow = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
  align-items: center;

  label {
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  input {
    margin-top: 5px;
    width: 70%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
`;

const AddOptionButtom = styled.button`
  padding: 10px;
  border: none;
  border-radius: 4px;
  background-color: #000;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.2s;
`;

const FormActions = styled.div`
  display: flex;
  gap: 10px;
  margin-top: 20px;

  button {
    margin-bottom: 10px;
    flex: 1;
    padding: 10px;
    border: none;
    border-radius: 4px;
    background-color: #000;
    color: #fff;
    cursor: pointer;
    transition: background-color 0.2s;
  }

  button:hover {
    background-color: #2980b9;
  }
`;

const ModeForm = () => {
  const modeformIsOpen = useSelector((state) => state.modeform.isOpen);
  const isRecipeEditing = useSelector(
    (state) => state.modeform.isRecipeEditing,
  );
  const dispatch = useDispatch();
  const [newMode, setNewMode] = useState(
    useSelector((state) => state.modeform.modeForm),
  );

  const filePath = useSelector((state) => state.file.file.file);
  const handleInputChange = (field, value) => {
    setNewMode((prevMode) => ({
      ...prevMode,
      [field]: value,
    }));
  };

  const handleFeatureInputChange = (type, index, field, value) => {
    setNewMode((prevMode) => {
      const updatedMode = { ...prevMode };
      if (field === 'options') {
        updatedMode[type][index].options = updatedMode[type][index].options.map(
          (option, optionIndex) => {
            if (optionIndex === value.index) {
              // Update the specific option by matching index
              return { ...option, ...value.data };
            }
            return option;
          },
        );
      } else {
        updatedMode[type][index] = {
          ...updatedMode[type][index],
          [field]: value,
        };
      }
      return updatedMode;
    });
  };

  const handleAddStartFeature = () => {
    setNewMode((prevMode) => ({
      ...prevMode,
      start: [
        ...prevMode.start,
        { featureId: '', options: [{ controlId: '', option: '' }] }, // Initialize a new start feature with one option
      ],
    }));
  };

  const handleAddEndFeature = () => {
    setNewMode((prevMode) => ({
      ...prevMode,
      end: [
        ...prevMode.end,
        { featureId: '', options: [{ controlId: '', option: '' }] },
      ],
    }));
  };

  const handleDeleteStartFeature = () => {
    if (newMode.start.length > 0) {
      setNewMode((prevMode) => {
        const updatedMode = { ...prevMode };
        updatedMode.start.pop();
        return updatedMode;
      });
    }
  };

  const handleDeleteEndFeature = () => {
    if (newMode.end.length > 0) {
      setNewMode((prevMode) => {
        const updatedMode = { ...prevMode };
        updatedMode.end.pop();
        return updatedMode;
      });
    }
  };

  const handleAddOption = (type, index) => {
    setNewMode((prevMode) => {
      const updatedMode = { ...prevMode };
      updatedMode[type][index].options.push({ controlId: '', option: '' });
      return updatedMode;
    });
  };

  const handleDeleteOption = (type, featureIndex, optionIndex) => {
    setNewMode((prevMode) => {
      const updatedMode = { ...prevMode };
      updatedMode[type][featureIndex].options.splice(optionIndex, 1);
      return updatedMode;
    });
  };

  const maxOptions = Math.max(
    ...newMode.start.map((startFeature) => startFeature.options.length),
    ...newMode.end.map((endFeature) => endFeature.options.length),
  );

  const modalWidth = useMemo(() => {
    const baseWidth = 500;
    const additionalWidthPerOption = 50;
    return baseWidth + maxOptions * additionalWidthPerOption;
  }, [maxOptions]);

  const handleSaveMode = async () => {
    dispatch({ type: 'LOADING' });

    try {
      const dataToSave = {
        groupId: newMode.groupId || '',
        groupName: newMode.groupName || '',
        modeId: newMode.modeId || '',
        name: newMode.name || '',
        availabilityLevel: newMode.availabilityLevel || '',
        type: newMode.type || '',
        start: newMode.start || [],
        end: newMode.end || [],
        created_at: newMode.created_at || '',
      };

      if (isRecipeEditing) {
        // Recipe 내 Mode Editing
        let fileData = await axios.get(filePath);
        fileData.data = fileData.data.map((item) =>
          item.modeId === dataToSave.modeId ? dataToSave : item,
        );

        const postData = {
          file_path: filePath,
          file_data: fileData,
        };
        axios({
          method: 'POST',
          url: 'http://127.0.0.1:8000/api/edit_file/',
          data: postData,
          headers: {
            'Content-Type': 'application/json',
            'X-CSRFToken': getCookie('csrftoken'),
          },
        })
          .then((response) => {
            if (response.data['success']) {
              dispatch({
                type: 'SET_JSON',
                editedJson: JSON.stringify(fileData.data, null, 2),
              });
              toast.success('모드 수정 성공!');
            } else {
              toast.error('모드 수정 에러!');
            }
          })
          .catch((error) => {
            console.log(error);
            toast.error(error);
          });
      } else {
        // Store 내 Mode Editing
        if (dataToSave['created_at'] === '') {
          await axios.post(
            'http://127.0.0.1:8000/api/upload-json-data/',
            dataToSave,
          );
        } else {
          await axios.put(
            `http://127.0.0.1:8000/api/json-data-edit/${dataToSave['created_at']}/`,
            dataToSave,
          );
        }
      }
    } catch (error) {
      console.error('Mode 저장 중 오류:', error);
    } finally {
      // 서버에서 업데이트된 전체 데이터를 가져옴
      const response = await axios.get(
        'http://127.0.0.1:8000/api/json-data-list/',
      );
      if (response.status === 200) {
        dispatch({ type: 'SET_KANBAN', kanban: response.data });
      } else {
        console.error('Failed to fetch JSON data list');
      }
      dispatch({ type: 'MODEFORM_CLOSE' });
      dispatch({ type: 'NOT_LOADING' });
    }
  };

  return (
    <Modal
      isOpen={modeformIsOpen}
      onRequestClose={() => dispatch({ type: 'MODEFORM_CLOSE' })}
      contentLabel="Create Mode Modal"
      style={customModalStyles}
    >
      <FormContainer>
        <FormHeader>Create a New Mode</FormHeader>
        <FormBody>
          <FormRow>
            <label>
              Group ID:
              <input
                type="text"
                value={newMode.groupId}
                onChange={(e) => handleInputChange('groupId', e.target.value)}
              />
            </label>
            <label>
              Group Name:
              <input
                type="text"
                value={newMode.groupName}
                onChange={(e) => handleInputChange('groupName', e.target.value)}
              />
            </label>
          </FormRow>
          <FormRow>
            <label
              title={isRecipeEditing ? 'Mode ID는 수정할 수 없습니다' : ''}
            >
              {isRecipeEditing ? (
                <>Mode ID: {newMode.modeId}</>
              ) : (
                <>
                  Mode ID:
                  <input
                    type="text"
                    value={newMode.modeId}
                    onChange={(e) =>
                      handleInputChange('modeId', e.target.value)
                    }
                  />
                </>
              )}
            </label>
            <label>
              Name:
              <input
                type="text"
                value={newMode.name}
                onChange={(e) => handleInputChange('name', e.target.value)}
              />
            </label>
          </FormRow>
          <FormRow>
            <label>
              Availability Level:
              <input
                type="text"
                value={newMode.availabilityLevel}
                onChange={(e) =>
                  handleInputChange('availabilityLevel', e.target.value)
                }
              />
            </label>
          </FormRow>

          {newMode.start.map((startFeature, startIdx) => (
            <FormRow key={`start-${startIdx}`}>
              <label>
                Start Feature ID:
                <input
                  type="text"
                  value={startFeature.featureId}
                  onChange={(e) =>
                    handleFeatureInputChange(
                      'start',
                      startIdx,
                      'featureId',
                      e.target.value,
                    )
                  }
                />
              </label>
              <AddOptionButtom
                onClick={() => handleAddOption('start', startIdx)}
              >
                Add Option
              </AddOptionButtom>

              {startFeature.options.map((option, optionIdx) => (
                <div key={`start-${startIdx}-option-${optionIdx}`}>
                  <label>
                    Option Control ID:
                    <input
                      type="text"
                      value={option.controlId}
                      onChange={(e) =>
                        handleFeatureInputChange('start', startIdx, 'options', {
                          index: optionIdx,
                          data: { ...option, controlId: e.target.value },
                        })
                      }
                    />
                  </label>

                  <label>
                    Option:
                    <input
                      type="text"
                      value={option.option}
                      onChange={(e) =>
                        handleFeatureInputChange('start', startIdx, 'options', {
                          index: optionIdx,
                          data: { ...option, option: e.target.value },
                        })
                      }
                    />
                  </label>

                  <FormActions>
                    <button
                      onClick={() =>
                        handleDeleteOption('start', startIdx, optionIdx)
                      }
                    >
                      Delete Option
                    </button>
                  </FormActions>
                </div>
              ))}
            </FormRow>
          ))}

          <FormActions>
            <button onClick={handleAddStartFeature}>Add Start Feature</button>
            <button onClick={handleDeleteStartFeature}>
              Delete Start Feature
            </button>
          </FormActions>

          {newMode.end.map((endFeature, endIdx) => (
            <FormRow key={`end-${endIdx}`}>
              <label>
                End Feature ID:
                <input
                  type="text"
                  value={endFeature.featureId}
                  onChange={(e) =>
                    handleFeatureInputChange(
                      'end',
                      endIdx,
                      'featureId',
                      e.target.value,
                    )
                  }
                />
              </label>
              <AddOptionButtom onClick={() => handleAddOption('end', endIdx)}>
                Add Option
              </AddOptionButtom>
              {endFeature.options.map((option, optionIdx) => (
                <div key={`end-${endIdx}-option-${optionIdx}`}>
                  <label>
                    Option Control ID:
                    <input
                      type="text"
                      value={option.controlId}
                      onChange={(e) =>
                        handleFeatureInputChange('end', endIdx, 'options', {
                          index: optionIdx,
                          data: { ...option, controlId: e.target.value },
                        })
                      }
                    />
                  </label>

                  <label>
                    Option:
                    <input
                      type="text"
                      value={option.option}
                      onChange={(e) =>
                        handleFeatureInputChange('end', endIdx, 'options', {
                          index: optionIdx,
                          data: { ...option, option: e.target.value },
                        })
                      }
                    />
                  </label>

                  <FormActions>
                    <button
                      onClick={() =>
                        handleDeleteOption('end', endIdx, optionIdx)
                      }
                    >
                      Delete Option
                    </button>
                  </FormActions>
                </div>
              ))}
            </FormRow>
          ))}

          <FormActions>
            <button onClick={handleAddEndFeature}>Add End Feature</button>
            <button onClick={handleDeleteEndFeature}>Delete End Feature</button>
          </FormActions>

          <FormActions>
            <button onClick={handleSaveMode}>Save Mode</button>
          </FormActions>
        </FormBody>
      </FormContainer>
    </Modal>
  );
};

export default ModeForm;
