import React from 'react';
import styled from 'styled-components';
import { useDropzone } from 'react-dropzone';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios';
import getCookie from '../Routers/getCookie';
import { useDispatch } from 'react-redux';

const Dropzone = styled.div`
  border: 1px dashed #333;
  border-radius: 4px;
  padding: 10px;
  margin: 5px 0;
  text-align: center;
  padding: 20px;
`;

const Paragraph = styled.p`
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-weight: bold;
  margin: 0;
`;

function showToast(message) {
  toast.success(message, {
    position: 'bottom-right',
    autoClose: 2000,
    style: {
      backgroundColor: '#333',
      color: '#fff',
      border: '2px solid #fff',
    },
  });
}

const DragComponent = ({ setFiles }) => {
  const dispatch = useDispatch();

  const onDrop = (acceptedFiles) => {
    if (acceptedFiles.length === 0) {
      showToast('파일을 선택하세요.');
      return;
    }

    dispatch({ type: 'LOADING' });

    const formData = new FormData();
    formData.append('file', acceptedFiles[0]);

    axios
      .post('http://127.0.0.1:8000/api/upload/', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
          'X-CSRFToken': getCookie('csrftoken'),
        },
      })
      .then((response) => {
        if (response.status === 200) {
          showToast('파일 업로드 성공!');
          return axios.get('http://127.0.0.1:8000/api/files/');
        } else {
          showToast('파일 업로드 실패!');
          throw new Error('파일 업로드 실패!');
        }
      })
      .then((response) => {
        return response.data;
      })
      .then((data) => {
        console.log(data);
        const decodedFiles = data.map((file) => ({
          ...file,
          decoded_name: decodeURIComponent(file.file.split('/').pop()),
        }));

        setFiles(decodedFiles);
      })
      .catch((error) => {
        showToast('파일 업로드 중 오류 발생!');
        console.error('파일 업로드 오류:', error);
      })
      .finally(() => {
        dispatch({ type: 'NOT_LOADING' });
      });
  };

  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    maxFiles: 1,
  });

  return (
    <Dropzone {...getRootProps()} className={`dropzone ${isDragActive ? 'active' : ''}`}>
      <input {...getInputProps()} />
      <Paragraph>🤠 파일을 드래그하여 업로드하거나, 클릭하여 파일을 선택하세요.</Paragraph>
    </Dropzone>
  );
};

export default DragComponent;
