import React from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import MainComponent from '../Components/MainComponent';
import RegistrationComponent from '../Components/RegistrationComponent';
import UploadComponent from '../Components/UploadComponent';
import JsonKanbanComponent from '../Components/JsonControls/JsonKanbanComponent';
import UserProfileComponent from '../Components/UserProfileComponent';
import LoginComponent from '../Components/LoginComponent';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { PrivateRouter } from '../Routers/PrivateRouter';

const App = () => {
  return (
    <Router>
      <div>
        <Routes>
          <Route element={<PrivateRouter />}>
            <Route path="/" element={<MainComponent />} />
          </Route>
          <Route element={<PrivateRouter />}>
            <Route path="/upload" element={<UploadComponent />} />
          </Route>
          <Route element={<PrivateRouter />}>
            <Route path="/json/:jsonUrl" element={<JsonKanbanComponent />} />
          </Route>
          <Route element={<PrivateRouter />}>
            <Route path="/user_profile" element={<UserProfileComponent />} />
          </Route>
          <Route path="/registration" element={<RegistrationComponent />} />
          <Route path="/login" element={<LoginComponent />} />
        </Routes>
        <ToastContainer /> {/* Place it inside the div */}
      </div>
    </Router>
  );
};

export default App;
