// JsonKanbanComponent.jsx
import React, { useMemo } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd';
import { useSelector, useDispatch } from 'react-redux';

const VerticalKanbanList = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
`;

const KanbanCard = styled.div`
  margin: 5px;
  padding: 10px;
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-size: 0.8rem;
  color: #000;
  border: 1px solid #555;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition:
    background-color 0.2s,
    transform 0.2s;

  &:active {
    cursor: pointer;
  }

  &:hover {
    background-color: #f2f2f2;
  }
`;

const RenderJsonAsKanbanComponent = ({ data, depth = 0 }) => {
  const dispatch = useDispatch();
  const loading = useSelector((state) => state.loading.loading);

  if (loading) {
    return <div> 칸반 데이터를 불러오는 중입니다 ...</div>;
  }

  if (!data) {
    return <div>No JSON data available.</div>;
  }

  const jsonData = JSON.parse(data);

  return (
    <Droppable
      key={`render-kanban-key-${depth}`}
      droppableId={`render-kanban-${depth}`}
      type="CARD"
    >
      {(provided) => (
        <div ref={provided.innerRef} {...provided.droppableProps}>
          <VerticalKanbanList>
            {Object.entries(jsonData).map(([key, value], index) => {
              const isObject = typeof value === 'object' && value !== null;
              return (
                <Draggable
                  key={`draggable-${key}-${index}`}
                  draggableId={`draggable-${key}-${index}`}
                  index={index}
                >
                  {(dragProvided) => (
                    <KanbanCard
                      ref={dragProvided.innerRef}
                      {...dragProvided.draggableProps}
                      {...dragProvided.dragHandleProps}
                    >
                      <strong>{key}:</strong>
                      {isObject ? (
                        <div>
                          <RenderJsonAsKanbanComponent
                            data={JSON.stringify(value)}
                            depth={depth + 1}
                          />
                        </div>
                      ) : (
                        <div>{value}</div>
                      )}
                    </KanbanCard>
                  )}
                </Draggable>
              );
            })}
            {provided.placeholder}
          </VerticalKanbanList>
        </div>
      )}
    </Droppable>
  );
};

export default RenderJsonAsKanbanComponent;
