// ModalReducer.js
const initialState = {
  modalIsOpen: false, // 모달 상태를 저장하는 불리언
  modalContent: '', // 모달 내용을 저장하는 변수
};

const modalReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'OPEN_MODAL': // 모달 열기
      return {
        ...state,
        modalIsOpen: true,
        modalContent: action.content,
      };
    case 'CLOSE_MODAL': // 모달 닫기
      return {
        ...state,
        modalIsOpen: false,
        modalContent: '',
      };
    default:
      return state;
  }
};

export default modalReducer;
