import React, { useState } from 'react';
import BaseComponent from './BaseComponent';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { toast } from 'react-toastify';
import axios from 'axios';
import getCookie from '../Routers/getCookie';
import { ClipLoader } from 'react-spinners'; // You can choose a specific loader from the library

import { useSelector, useDispatch } from 'react-redux';
import { initialState } from '../Reducers/UserReducer';
import reducerStore from '../Reducers/ReducerStore';

const RegistrationContainer = styled.div`
  text-align: center;
  margin: 30px;
`;

const RegistrationTitle = styled.h2`
  font-size: 24px;
  margin-bottom: 20px;
`;

const RegistrationForm = styled.form`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
`;

const FormGroup = styled.div`
  margin: 10px 0;
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
`;

const Label = styled.label`
  display: block;
  font-weight: bold;
  width: 25%;
`;

const InputField = styled.input`
  flex: 1;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 65%;
  font-size: 16px;
`;

const EmptyField = styled.div`
  display: flex;
  width: 10%;
  justify-content: right;
  align-items: center;
  text-align: center;
`;

const VerifyButton = styled.button`
  font-family: Arial, sans-serif;
  background-color: #fff;
  color: #000;
  border: none;
  border-radius: 5px;
  font-size: 13px;
  cursor: pointer;
  transition: background-color 0.2s;
  padding: 10px;
  box-sizing: border-box;
  margin-left: 20px;
`;

const RegisterButton = styled.button`
  font-family: Arial, sans-serif;
  background-color: #fff;
  margin: 10px;
  color: #000;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.2s;
  box-sizing: border-box;
`;

const SpinnerContainer = styled.div`
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const BlurOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(
    255,
    255,
    255,
    0.5
  ); // Adjust the background color and opacity as needed
  backdrop-filter: blur(5px); // Adjust the blur amount as needed
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const RegistrationComponent = () => {
  const navigate = useNavigate();
  const [showVerificationForm, setShowVerificationForm] = useState(false);
  const [verifyOk, setVerfyOk] = useState(false);
  const [loading, setLoading] = useState(false); // Loading state
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password1: '',
    password2: '',
    first_name: '',
    last_name: '',
    verificationCode: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleVerificationClick = async (e) => {
    setLoading(true);
    if (!formData.email) {
      toast.error('이메일을 입력해주세요');
      setLoading(false);
      return;
    }

    // axios 활용하여 인증번호를 GET
    e.preventDefault();
    axios({
      method: 'POST',
      url: 'http://127.0.0.1:8000/api/verify_email/',
      data: formData.email,
      headers: {
        'Content-Type': 'application/json',
        'X-CSRFToken': getCookie('csrftoken'),
      },
    })
      .then((response) => {
        if (response.status === 200) {
          toast.success('입력하신 메일로 인증번호를 전송했습니다.');
          setShowVerificationForm(true);
        } else if (response.status === 401) {
          toast.error('서버 오류로 실패했습니다.');
        } else {
          toast.error('알 수 없는 오류가 발생했습니다.');
        }
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const handleVerifyClick = async (e) => {
    setLoading(true);
    // 추가: 인증 확인을 처리하는 로직을 여기에 추가합니다.
    // 서버로 인증 확인 요청을 보낼 수 있습니다.
    if (!formData.verificationCode) {
      toast.error('인증번호를 입력해주세요');
      setLoading(false);
      return;
    }
    e.preventDefault();
    axios({
      method: 'POST',
      url: 'http://127.0.0.1:8000/api/check_email/',
      data: {
        email: formData.email,
        verificationCode: formData.verificationCode,
      },
      headers: {
        'Content-Type': 'application/json',
        'X-CSRFToken': getCookie('csrftoken'),
      },
    })
      .then((response) => {
        if (response.status === 200) {
          toast.success('입력하신 메일로 인증에 성공했습니다.');
          setShowVerificationForm(false);
          setVerfyOk(true);
        } else if (response.status === 302) {
          toast.error('입력하신 인증번호가 잘못됐습니다.');
        } else {
          toast.error('알수 없는 오류로 발생했습니다.');
        }
      })
      .catch((error) => {
        toast.error(error);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const handleSubmit = async (e) => {
    setLoading(true);
    // formData 중에 빈 칸이 있다면 알리는 Toast 로직
    const emptyFields = Object.keys(formData).filter(
      (key) => !formData[key] && key != 'verificationCode',
    );

    if (emptyFields.length > 0) {
      toast.error('모든 필드를 작성해주세요.');
      setLoading(false);
      return;
    }
    if (!verifyOk) {
      toast.error('이메일 인증이 완료되지 않았습니다.');
      setLoading(false);
      return;
    }
    if (formData.password1 != formData.password2) {
      toast.error('입력하신 비밀번호가 다릅니다. 확인 후 제출해주세요.');
      setLoading(false);
      return;
    }

    e.preventDefault();
    axios({
      method: 'POST',
      url: 'http://127.0.0.1:8000/api/registration/',
      data: JSON.stringify(formData),
      headers: {
        'Content-Type': 'application/json',
        'X-CSRFToken': getCookie('csrftoken'),
      },
    })
      .then((response) => {
        if (response.status === 200) {
          console.log('Registration successful');
          toast.success('가입에 성공했습니다. 다시 로그인해주세요.');
          navigate('/login', { replace: true });
        } else if (response.status === 302) {
          console.error('Registration failed (ID 중복)');
          toast.error(
            '이미 존재하는 사용자입니다. 다른 사용자 이름을 선택해주세요.',
          );
        } else if (response.status === 301) {
          console.error('Registration failed (서버 오류)');
          toast.error('서버 오류로 사용자 등록에 실패했습니다.');
        } else {
          console.error('Unknown error');
          toast.error('알 수 없는 오류가 발생했습니다.');
        }
      })
      .catch((error) => {
        console.error('Registration failed (네트워크 오류)');
        toast.error('네트워크 오류로 사용자 등록에 실패했습니다.');
      })
      .finally(() => {
        setLoading(false);
      });
  };

  return (
    <BaseComponent>
      <RegistrationContainer>
        <RegistrationTitle>USER REGISTRATION</RegistrationTitle>
        {loading && (
          <BlurOverlay>
            <SpinnerContainer>
              <ClipLoader size={35} color={'#000'} />
            </SpinnerContainer>
          </BlurOverlay>
        )}
        <RegistrationForm onSubmit={handleSubmit} autocomplete="off">
          <FormGroup>
            <Label htmlFor="username">Username</Label>
            <InputField
              type="text"
              id="username"
              name="username"
              value={formData.username}
              onChange={handleChange}
            />
            <EmptyField></EmptyField>
          </FormGroup>
          <FormGroup>
            <Label htmlFor="email">Email</Label>
            <InputField
              type="email"
              id="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
            />
            {!verifyOk ? (
              showVerificationForm ? (
                <EmptyField />
              ) : (
                <EmptyField>
                  <VerifyButton type="button" onClick={handleVerificationClick}>
                    VERIFY
                  </VerifyButton>
                </EmptyField>
              )
            ) : (
              <EmptyField> Verified!✅ </EmptyField>
            )}
          </FormGroup>
          {showVerificationForm ? (
            <FormGroup>
              <Label htmlFor="verificationCode">인증번호</Label>
              <InputField
                type="text"
                id="verificationCode"
                name="verificationCode"
                value={formData.verificationCode}
                onChange={handleChange}
              />
              <EmptyField>
                <VerifyButton type="button" onClick={handleVerifyClick}>
                  CHECK
                </VerifyButton>
              </EmptyField>
            </FormGroup>
          ) : (
            <EmptyField />
          )}
          <FormGroup>
            <Label htmlFor="password1">Password</Label>
            <InputField
              type="password"
              id="password1"
              name="password1"
              value={formData.password1}
              onChange={handleChange}
            />
            <EmptyField></EmptyField>
          </FormGroup>
          <FormGroup>
            <Label htmlFor="password2">Confirm Password</Label>
            <InputField
              type="password"
              id="password2"
              name="password2"
              value={formData.password2}
              onChange={handleChange}
            />
            <EmptyField></EmptyField>
          </FormGroup>
          <FormGroup>
            <Label htmlFor="first_name">first_name</Label>
            <InputField
              type="text"
              id="first_name"
              name="first_name"
              value={formData.first_name}
              onChange={handleChange}
            />
            <EmptyField></EmptyField>
          </FormGroup>
          <FormGroup>
            <Label htmlFor="last_name">last_name</Label>
            <InputField
              type="text"
              id="last_name"
              name="last_name"
              value={formData.last_name}
              onChange={handleChange}
            />
            <EmptyField></EmptyField>
          </FormGroup>
        </RegistrationForm>
        <RegisterButton type="submit" onClick={handleSubmit}>
          Register
        </RegisterButton>
        <RegisterButton onClick={() => navigate('/login')} disabled={loading}>
          Back
        </RegisterButton>
      </RegistrationContainer>
    </BaseComponent>
  );
};

export default RegistrationComponent;
