import { useCallback, useEffect, useState } from 'react';
 
const idleTimeout = 1000 * 60 * 30; // 30 minutes
 
const useEventTimeout  = (
  callback: () => void,
  events: string[] = ['click', 'keypress'],
  timeout: number = idleTimeout,
  warning : number = timeout - (1000*60)
  
 ) => {
  const [idleTime, setIdleTime] = useState(new Date());
 
  const resetTimer = useCallback(() => {
    setIdleTime(new Date());
  }, []);
 
  useEffect(() => {
    const eventListeners = events.map((event) => {
      window.addEventListener(event, resetTimer);
      return () => {
        window.removeEventListener(event, resetTimer);
      };
    });

    return function cleanup() {
      eventListeners.forEach((removeListener) => removeListener());
    };
  }, []);
 
  useEffect(() => {
    const idleTimer = setTimeout(() => {
      callback("제한 시간 종료!");
    }, timeout);
 
    return function cleanup() {
      clearTimeout(idleTimer);
    };
  }, [idleTime, timeout]);

  useEffect(() => {
    const idleTimer = setTimeout(() => {
      callback("오랜 시간동안 활동이 없었습니다. 1분 동안 활동하지 않는 경우, 로그아웃 처리됩니다.");
    }, warning);
 
    return function cleanup() {
      clearTimeout(idleTimer);
    };
  }, [idleTime, warning]);
};
 
export default useEventTimeout;